  <h1 class="h1">Badges</h1>
  <p>
    Vuemann intègre un ensemble de styles pour les badges avec des couleurs prédéfinies.
  </p>
  <p>
    La classe <span class="badge-code">.badge</span> permet d'appliquer le style de base du badge. La structure suivante <span class="badge-code">.badge-{color}</span> 
